module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">src</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"color: #d53700; padding-bottom: 1.8rem\">required</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">The source string for the image which will be loaded and displayed.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">variant</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'default'</td><td style=\"padding-bottom: 1.8rem\">Sets the style of the image from the following values; <code style=\"white-space: pre-wrap\">default</code>, <code>rounded</code>, <code>circle</code>, <code>thumbnail</code>.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isFluid</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Sets the fluid behavior of the image, which is <code style=\"white-space: pre-wrap\">nonfluid</code> by default.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">alt</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">' '</td><td style=\"padding-bottom: 1.8rem\">The text content that specifies an alternative text for an image.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">placeholder</td><td style=\"padding-bottom: 1.8rem\">element</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">A React element which will be displayed during loading and in case of src load failure.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">height</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Sets the height of the image.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">width</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Sets the width of the image.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onLoad</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Function to be executed when the image load is successful.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onError</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Function to be executed when the image load errors.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">fit</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'fill'</td><td style=\"padding-bottom: 1.8rem\">Sets the <code style=\"white-space: pre-wrap\">object-fit</code> style of the image from the following values; <code>cover</code>, <code>contain</code>, <code>fill</code>, <code>scale-down</code>, <code>none</code>.</td></tr></tbody></table>"